import React from 'react';
import { Navigate } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';

/**
 * ProtectedRoute Component
 * Verifica si el usuario est autenticado antes de permitir acceso a una ruta
 * Si no est autenticado, redirige a la pgina de login
 * 
 * Props:
 * - children: Componente a renderizar si est autenticado
 * - requiresAdmin: Requiere que sea Admin? (default: false)
 */
const ProtectedRoute = ({ children, requiresAdmin = false }) => {
  const { isAuthenticated, user, loading, hasRole } = useAuth();

  // Mostrar loading mientras se verifica la autenticacin
  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-screen bg-black">
        <div className="animate-spin rounded-full h-8 w-8 border-t-2 border-b-2 border-green-400"></div>
      </div>
    );
  }

  // Si no est autenticado, redirigir al login
  if (!isAuthenticated) {
    return <Navigate to="/" replace />;
  }

  // Si requiere Admin, validar el rol del usuario
  if (requiresAdmin && !hasRole('Admin')) {
    console.warn(
      `Acceso denegado: Se requiere rol de Admin. Usuario actual: ${user?.role || 'sin rol'}`
    );
    return <Navigate to="/dashboard" replace />;
  }

  // Si est autenticado y cumple requisitos, renderizar el componente
  return children;
};

export default ProtectedRoute;
